.tag {
  @include flexbox-center;
  min-width: 8.4rem;
  height: 1.8rem;
  border-radius: 0.5rem;
  margin: 0 1rem 1rem 0;
  background-color: $color-primary;
  padding: 0 1rem;
  h3 {
    font-size: 1rem;
    font-weight: 500;
    color: $color-white;
  }
  @include tablet {
    font-size: 14px;
  }
}
