.carousel {
  overflow: hidden;
  height: 255px;
  border-radius: 1rem;
  position: relative;
  @include desktop {
    height: 415px;
  }
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .arrow-container {
    @include flexbox-center;
    height: 36px;
    width: 36px;
    position: absolute;
    top: 110px;
    border-radius: 50%;
    @include desktop {
      height: 80px;
      width: 80px;
      top: 40%;
    }
    &__right {
      right: 0;
    }
    &__left {
      transform: rotate(180deg);
    }
    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      background-color: $color-tertiary;
      opacity: 0;
      border-radius: 50%;
      opacity: 0;
    }
    &:active {
      &::after {
        opacity: 0.5;
      }
    }
    @include desktop {
      &:hover {
        &::after {
          opacity: 0.5;
          transition: opacity 250ms;
        }
      }
    }
  }
  .arrow {
    height: 2.4rem;
    width: 2.4rem;
    fill: white;
    z-index: 1;
    @include desktop {
      height: 80px;
      width: 80px;
    }
    @include desktop-l {
      height: 100px;
      width: 100px;
    }
  }
}
